.navbar-div {
    margin: 5% 5%;
    padding: 0% 0%;

    display: flex;
    flex-direction: row;

    border-top: 3px solid black;
    border-bottom: 3px solid black;

    justify-content: space-between;
}

.logo-outer {
    margin-bottom: 10px;
}

.navigator-outer {
    flex: 0.5;

    display: flex;
    flex-direction: row;
    
    align-self: flex-end;
    justify-content: space-around;

    margin-bottom: 1%;
}
.navigator {
    font-family: "Inter";
    font-size: 24px;
}